import * as React from 'react';
import { ResponseModel } from '../../services/models';
import styled from '../../styled-components';
import { ResponseView } from './Response';
import { OptionsContext } from '../OptionsProvider';
import { ResponseDetailsWrap } from './styled.elements';
import { ResponseDetails } from './ResponseDetails';
const ResponsesHeader = styled.h3.withConfig({
  componentId: "sc-1xi9kpc-0"
})(["font-size:18px;padding:0.2em 0;margin:3em 0 1.1em;color:#253137;font-weight:normal;"]);
export interface ResponseListProps {
  responses: ResponseModel[];
}
export class ResponsesList extends React.PureComponent<ResponseListProps> {
  render() {
    const {
      responses
    } = this.props;

    if (!responses || responses.length === 0) {
      return null;
    }

    return <OptionsContext.Consumer>
        {options => {
        if (!options.flattenResponseView) {
          return <div>
                  <ResponsesHeader> Responses </ResponsesHeader>
                  {responses.map(response => {
              return <ResponseView key={response.code} response={response} />;
            })}
                </div>;
        } else {
          return responses.map(response => {
            return <ResponseDetailsWrap key={response.code}>
                    <ResponseDetails response={response} />
                  </ResponseDetailsWrap>;
          });
        }
      }}
      </OptionsContext.Consumer>;
  }

}